.footer {
	padding: 50px 0 30px 0;
	background: #001826;
	color: $white;
	@media (max-width: 375px) {
	}
	@media (max-width: 320px) {
	}
	&__logo {
		margin-bottom: 30px;
	}
	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	&__item {
		&--menu {
			margin-right: 150px;
		}
	}
	&__title {
		font-weight: 700;
		font-size: 20px;
		line-height: 115%;
		color: $white;
		margin-bottom: 15px;
		span {
			margin-right: 25px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		&-item {
			margin-right: 30px;
			margin-bottom: 30px;
		}
		&-link {
			font-weight: 700;
			font-size: 18px;
			color: $white;
		}
	}
	&__right {
		display: flex;
		flex-wrap: wrap;
	}
	&__menu {
		&-item {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__text {
		font-size: 20px;
		line-height: 115%;
		color: $white;
	}
	&__contacts {
		margin-top: 25px;
	}
	&__schedule,
	&__phone {
		font-family: "Open Sans", sans-serif;
	}
	&__bottom {
		text-align: center;
		margin-top: 46px;
		position: relative;
	}
	&__descr {
		margin-top: 5px;
	}
	&__copy {
		font-size: 14px;
		text-align: center;
		margin-top: 78px;
	}
	&__acclangblock {
		display: flex;
		justify-content: flex-end;
		@media (max-width: 375px) {
		}
		@media (max-width: 320px) {
		}
	}
	&__accounts {
		display: flex;
		position: relative;
		align-items: center;
		font-weight: 500;
		font-size: 18px;
		line-height: 19px;
		color: #ffffff;
		@media (max-width: 375px) {
			font-size: 12px;
			line-height: 14px;
			position: absolute;
			top: 0;
			left: 0;
		}
		@media (max-width: 320px) {
		}
		svg {
			fill: white;
			@media (max-width: 375px) {
				width: 14;
				height: 17px;
				margin-left: -8px;
			}
		}
		span {
			margin-right: 9px;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
		}
		&-menu {
			font-family: "Raleway";
			font-style: normal;
			font-weight: 500;
			font-size: 16px;
			line-height: 19px;
			@media (max-width: 375px) {
				font-size: 12px;
				line-height: 14;
			}
			@media (max-width: 320px) {
			}
			button {
				color: #ffffff;
				&::before {
					content: " ";
					background: #ffffff;
					width: 8px;
					height: 1px;
					transition: 0.3s ease 0s;
					transform: translate(0, -50%) rotate(-45deg);
					position: absolute;
					right: -20px;
					top: 15px;
					@media (max-width: 375px) {
						top: 82px;
					}
					@media (max-width: 320px) {
					}
				}
				&::after {
					content: " ";
					background: #ffffff;
					width: 8px;
					height: 1px;
					transition: 0.3s ease 0s;
					transform: translate(0, -50%) rotate(45deg);
					position: absolute;
					right: -15px;
					top: 15px;
					@media (max-width: 375px) {
						top: 82px;
					}
					@media (max-width: 320px) {
					}
				}
				&:hover {
					&::before {
						transform: translate(0, -50%) rotate(45deg);
					}
					&::after {
						transform: translate(0, -50%) rotate(-45deg);
					}
				}
			}
		}
		&-drpd {
			visibility: hidden;
			position: absolute;
			width: 219px;
			height: 75px;
			display: flex;
			flex-direction: column;
			background: transparent;
			box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
			border: 1.5px solid white;
			border-radius: 36px;
			justify-content: center;
			align-items: center;
			top: 28px;
			transition: all 0.3s ease;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
			div {
				width: 200px;
				height: 1px;
				border-top: 1px solid #e3e3e3;
				@media (max-width: 375px) {
				}
				@media (max-width: 320px) {
				}
			}
		}
		&-link {
			font-size: 16px;
			margin: 7px 0 5px 0;
			color: white;
			transition: all 0.3s ease;
		}
	}
	&__languages {
		border: 1px solid white;
		padding: 5px 10px;
		margin-left: 45px;
		font-weight: 600;
		font-size: 14px;
		color: white;
		border-radius: 36px;
		@media (max-width: 375px) {
			margin-left: 40px;
		}
		@media (max-width: 320px) {
			margin-left: 25px;
		}
		&-wrapper {
			max-width: 92px;
			min-height: 102px;
			border-radius: 0px 0px 26px 26px;
			padding: 0 8px;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
		}
		&-items {
			border-bottom: 1px solid $white;
			padding: 10px;
			display: flex;
			font-size: 14px;
			align-items: center;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
			&:last-child {
				border-bottom: 0;
			}
			img {
				margin-right: 7px;
				@media (max-width: 375px) {
				}
				@media (max-width: 320px) {
				}
			}
		}
		&-point {
			display: flex;
			align-items: center;
			img {
				margin-right: 7px;
			}
		}
	}
}

.footer__accounts-link:hover {
	font-weight: bold;
}

.footer__accounts-menu:hover .footer__accounts-drpd {
	visibility: visible;
}
