@media (max-width: 1350px) {
   .reviews {
      &__item {
         width: 49%;
         max-width: 100%;
      }
   }
}
@media (max-width: 1000px) {
   .hero {
      &__inner {
         flex-direction: column;
      }
      &__info {
         max-width: 100%;
      }
   }
   .insurance {
      &__item {
         &-descr {
            font-size: 25px;
         }
      }
   }
   .scheme {
      &__items {
         display: none;
         &--mobail {
            display: block;
         }
         &-top,
         &-bottom {
            flex-direction: column;
         }
      }
      &__item {
         margin-bottom: 80px;
         &:last-child {
            margin-bottom: 0;
         }
         &--third {
            &::before {
               left: 158px;
               top: -43px;
            }
         }
         &--second {
            &::before {
               right: 335px;
               top: -35px;
            }
         }
         &--seconds {
            margin-left: auto;
         }
      }
   }
}
@media (max-width: 900px) {
   .insurance {
      &__item {
         padding: 30px;
      }
   }
   .reviews {
      &__items {
         flex-direction: column;
      }
      &__item {
         width: 100%;
      }
   }
}
@media (max-width: 850px) {
   .insurance {
      &__inner {
         flex-direction: column-reverse;
      }
   }
   .what {
      &__items {
         &-content {
            flex-wrap: wrap;
         }
      }
   }
}
@media (max-width: 700px) {
   .hero {
      padding: 80px 0;
      &__title {
         font-size: 40px;
      }
      &__descr {
         font-size: 20px;
      }
      &__item {
         &-descr {
            font-size: 16px;
         }
      }
   }
   .title {
      font-size: 35px;
   }
   .insurance {
      margin-bottom: 80px;
   }
   .faq {
      &__item {
         &-title {
            font-size: 24px;
         }
      }
   }
   .what {
      &__item {
         width: 100%;
         max-width: 100%;
         margin-bottom: 20px;
      }
   }
}
@media (max-width: 600px) {
   .hero {
      &__title {
         font-size: 30px;
      }
      &__item {
         &-descr {
            font-size: 14px;
         }
         &-icon {
            img {
               width: 35px;
               height: 35px;
            }
         }
      }
   }
   .faq {
      &__item {
         &-title {
            font-size: 20px;
         }
         &-content {
            font-size: 16px;
            padding-left: 20px;
         }
      }
   }
   .title {
      font-size: 30px;
   }
   .insurance {
      &__item {
         &-descr {
            font-size: 20px;
         }
         &-price {
            max-width: 94px;
            font-size: 25px;
         }
         &-span {
            font-size: 16px;
         }
      }
   }
   .scheme {
      &__items {
         &-top,
         &-bottom {
            align-items: start;
         }
      }
      &__item {
         &--second {
            &::before {
               right: 167px;
            }
         }
      }
   }
}
@media (max-width: 495px) {
   .hero {
      &__item {
         &-descr {
            font-size: 12px;
         }
      }
      &__descr {
         font-size: 18px;
      }
      &-icon {
         img {
            width: 30px;
            height: 30px;
         }
      }
      .title {
         font-size: 25px;
      }
   }
   .insurance {
      &__item {
         padding: 15px;
         margin-bottom: 15px;
         &-descr {
            font-size: 18px;
         }
      }
      &__btn {
         max-width: 100%;
         margin-top: 5px;
      }
   }
   .reviews {
      &__btn {
         max-width: 100%;
      }
   }
}
@media (max-width: 460px) {
   .hero {
      padding-bottom: 30px;
      &__item {
         &-descr {
            font-size: 10px;
         }
         &-icon {
            img {
               width: 26px;
               height: 26px;
            }
         }
      }
      &__descr {
         font-size: 18px;
         margin-bottom: 15px;
      }
      &__title {
         font-size: 26px;
      }
      &__overtitle {
         font-size: 7px;
         margin-bottom: 10px;
         &-line {
            width: 40px;
            margin-right: 5px;
         }
      }
   }
   .services {
      &__item {
         &-descr {
            font-size: 18px;
         }
      }
   }
   .insurance {
      padding-bottom: 30px;
      &__title {
         font-size: 25px;
         margin-bottom: 20px;
      }
      &__item {
         &-descr {
            font-size: 16px;
         }
         &-price {
            font-size: 18px;
         }
         &-span {
            font-size: 10px;
         }
      }
   }

   .btn {
      font-size: 16px;
   }
   .reviews {
      &__title {
         margin-bottom: 28px;
      }
      &__item {
         padding: 16px 10px 10px 10px;
         margin-bottom: 18px;
         &-title {
            font-size: 14px;
         }
         &-online {
            font-size: 11px;
         }
         &-descr {
            font-size: 12px;
         }
         &-google {
            img {
               width: 18px;
               height: 18px;
            }
         }
         &-info {
            margin-top: -14px;
            padding-left: 51px;
         }
         &-link {
            img {
               width: 19px;
               height: 16px;
            }
         }
         &-rating {
            padding-top: 2px;
         }
         &-icon {
            img {
               width: 46px;
               height: 46px;
            }
         }
      }
   }
   .faq {
      &__title {
         margin-bottom: 20px;
      }
      &__item {
         &-title {
            padding: 20px 0 20px 36px;
            font-size: 18px;
            &::before {
               top: 26px;
            }
         }
         &-content {
            font-size: 14px;
         }
      }
   }
   .scheme {
      &__title {
         margin-bottom: 20px;
      }
      &__item {
         &::before {
            width: 95px;
         }
         &-title {
            font-size: 20px;
         }
         &-text {
            font-size: 16px;
         }
         &-icon {
            img {
               width: 80px;
               height: 80px;
            }
         }
         &--first {
            &::before {
               top: 112px;
            }
         }
         &--third {
            &::before {
               left: -8px;
               top: -55px;
               transform: rotate(90deg);
            }
         }
      }
      &__items {
         &-bottom {
            .scheme__item {
               align-items: start;
            }
         }
      }
   }
   .what {
      &__item {
         min-height: 0;
         padding: 10px;
         &-title {
            font-size: 20px;
            min-height: 0;
         }
      }
   }
   .footer {
      &__title {
         font-size: 18px;
      }
      &__text {
         font-size: 18px;
      }
      &__menu {
         &-link {
            font-size: 18px;
         }
      }
   }
}

@media (max-width: 398px) {
   .hero {
      &__title {
         font-size: 24px;
         margin-bottom: 10px;
      }
      &__descr {
         font-size: 16px;
      }
   }
   .insurance {
      &__title {
         font-size: 20px;
      }
   }

   .btn {
      font-size: 14px;
      padding: 13px 14px;
      min-height: 46px;
   }
   .faq {
      &__item {
         &-title {
            padding: 15px 0 10px 36px;
            font-size: 14px;
            &::before {
               top: 20px;
            }
         }
         &-content {
            font-size: 12px;
            padding: 0 0 20px 0;
         }
      }
   }
   .scheme {
      &__item {
         &-title {
            font-size: 14px;
         }
         &-text {
            font-size: 12px;
         }
         &-icon {
            margin-right: 10px;
            img {
               width: 61px;
               height: 61px;
            }
         }
         &--second {
            &::before {
               transform: rotate(-42deg);
               right: 215px;
               top: -38px;
            }
         }
         &--first {
            &::before {
               top: 100px;
               right: 30px;
               transform: rotate(49deg);
            }
         }
      }
   }
   .footer {
      &__title {
         font-size: 16px;
      }
      &__text {
         font-size: 16px;
      }
      &__menu {
         &-link {
            font-size: 16px;
         }
      }
   }
}
@media (max-width: 370px) {
   .hero {
      &__title {
         font-size: 21px;
      }
      &__descr {
         font-size: 15px;
      }
      &__items {
         flex-direction: column;
         align-items: start;
      }
      &__item {
         margin-bottom: 6px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
   .reviews {
      &__item {
         &-info {
            margin-top: -3px;
            padding-left: 3px;
         }
      }
   }
   .scheme {
      &__item {
         &--second {
            &::before {
               right: 175px;
               top: -36px;
            }
         }
      }
   }
   .footer {
      &__title {
         font-size: 12px;
      }
      &__text {
         font-size: 12px;
      }
      &__menu {
         &-link {
            font-size: 10px;
         }
      }
   }
}
@media (max-width: 345px) {
   .insurance {
      &__item {
         padding: 8px;
         &-descr {
            font-size: 14px;
         }
      }
   }
   .title {
      font-size: 19px;
   }
}
