// fonts
// font-family: "Montserrat", sans-serif;
// font-family: "Open Sans", sans-serif;
// font-family: "Source Sans Pro", sans-serif;
//////////

body {
   font-family: "Raleway", sans-serif;
   color: $global;
   font-size: 16px;
   font-weight: 400;
}
*,
*::before,
*::after {
   box-sizing: border-box;
}
a {
   color: inherit;
   display: inline-block;
}
.wrapper {
   overflow: hidden;
}
.container {
   width: 100%;
   max-width: 1420px;
   padding: 0 15px;
   margin: 0 auto;
}

.btn {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   font-weight: 500;
   font-size: 18px;
   line-height: 21px;
   color: #ffffff;
   background: #4ac6e1;
   box-shadow: 0px 0px 13px rgba(182, 182, 182, 0.25);
   border-radius: 5px;
   transition: all 0.4s linear;
   padding: 15px 14px;
   min-height: 55px;
   &:hover {
      background: $purple;
   }
}
.title {
   font-family: "Source Sans Pro", sans-serif;
   font-weight: 700;
   font-size: 40px;
   line-height: 130%;
   color: $global;
   text-transform: uppercase;
   margin-bottom: 50px;
   span {
      color: $blue;
   }
}
.swiper-wrapp {
   width: 100%;
   max-width: 1260px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-top: 190px;
}
.swiper-buttons {
   position: relative;
   display: flex;
   align-items: center;
   justify-content: flex-end;
}
.swiper-button-next,
.swiper-button-prev {
   height: 71px !important;
   width: 71px !important;
   position: inherit !important;
   &::after {
      display: none;
   }
}
.swiper-button-next {
   background: url(../img/arrow-right.svg) no-repeat;
}
.swiper-button-prev {
   background: url(../img/arrow-left.svg) no-repeat;
   margin-right: 40px;
}
.swiper-line {
   width: 100%;
   max-width: 1043px;
   height: 1px;
   background: #dadada;
}
/* lang */
.sl-nav {
   display: inline;
}
.sl-nav ul {
   list-style: none;
   position: relative;
   display: inline-block;
}
.sl-nav-item {
   cursor: pointer;
}
.sl-nav li ul {
   display: none;
}
.sl-nav li:hover ul {
   position: absolute;
   top: 29px;
   right: -15px;
   display: block;
   z-index: 1;
}
.sl-nav li:hover .triangle {
   position: absolute;
   top: 15px;
   right: -10px;
   z-index: 10;
   height: 14px;
   overflow: hidden;
   width: 30px;
   background: transparent;
}
.sl-nav li ul li {
   width: 100%;
   position: relative;
   text-align: left;
   z-index: 2;
   font-size: 15px;
   color: $white;
}
.sl-nav li ul li span:hover,
.sl-nav li ul li span.active {
   opacity: 0.8;
}
.sl-flag {
   display: inline-block;
   box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.4);
   width: 15px;
   height: 15px;
   background: $purple;
   border-radius: 50%;
   position: relative;
   top: 2px;
   overflow: hidden;
}
.lang-icon {
   width: 25px;
   height: 25px;
   margin-right: 5px;
}
.lang-items a {
   display: flex;
   align-items: center;
}
