.hero {
	margin-top: 65px;
   padding: 120px 0 157px 0;
   &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__info {
      width: 100%;
      max-width: 688px;
   }
   &__phone {
      img {
         max-width: 100%;
      }
   }
   &__overtitle {
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 18px;
      color: $purple;
      margin-bottom: 30px;
      &-line {
         display: block;
         background: $purple;
         width: 66px;
         height: 0.5px;
         margin-right: 25px;
      }
   }
   &__title {
      font-weight: 700;
      font-size: 50px;
      line-height: 130%;
      color: $global;
      margin-bottom: 15px;
      span {
         display: inline-block;
         position: relative;
         color: $blue;
         border-bottom: 2px solid #f3f3f3;
         &::before {
            content: "";
            background: $blue;
            width: 40px;
            height: 2px;
            position: absolute;
            bottom: -2px;
            left: 0;
         }
      }
   }
   &__descr {
      font-weight: 300;
      font-size: 24px;
      line-height: 150%;
      color: $global;
      margin-bottom: 35px;
   }
   &__items {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__item {
      display: flex;
      align-items: center;
      &-icon {
         margin-right: 10px;
      }
      &-descr {
         font-size: 18px;
         line-height: 127%;
         color: $global;
      }
   }
}
.insurance {
   padding-bottom: 159px;
	&__title {
		height: 30px !important;
		margin-bottom: 50px !important;
		@media (max-width: 375px) {
			
				height: 60px !important;
				margin-bottom: 20px !important;
		}
	}
   &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__info {
      width: 100%;
      max-width: 800px;
   }
   &__photo {
      img {
