      }
   }
   &__item {
      width: 100%;
      max-width: 800px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border: 2px solid $purple;
      padding: 50px 56px;
      margin-bottom: 50px;
		
      &:last-child {
         margin-bottom: 30px;
      }
      &-descr {
         width: 100%;
         max-width: 468px;
         font-weight: 700;
         font-size: 30px;
         line-height: 116%;
         color: $global;
         margin-right: 5px;
      }
      &-price {
         width: 100%;
         max-width: 152px;
         color: $global;
         font-weight: 700;
         font-size: 40px;
         line-height: 25px;
         text-align: center;
      }
      &-spans {
         color: #4ac6e1;
      }
      &-span {
         color: $global;
         font-size: 20px;
         font-weight: 300;
      }
		&-btn {
			display: none;
			margin: 19px 0 0 15px;
			width: 132px;
			height: 51px;
			background: #FFFFFF;
			box-shadow: 0px 0px 13px rgba(182, 182, 182, 0.25);
			border-radius: 5px;
			font-family: 'Raleway';
			font-style: normal;
			font-weight: 500;
			font-size: 18px;
			line-height: 21px;

			color: #503B5C;
		}
