.faq {
   padding-bottom: 160px;
   &__item {
      border-bottom: 1px solid #dadada;
      &-title {
         position: relative;
         font-weight: 700;
         font-size: 30px;
         line-height: 116%;
         color: $global;
         cursor: pointer;
         padding: 40px 0 20px 44px;
         &::before {
            content: "";
            top: 46px;
            left: 0;
            background: url(../img/arrow-bottom.svg) no-repeat;
            position: absolute;
            transition: all 0.4s linear;
            width: 20px;
            height: 20px;
         }
         &._active {
            &::before {
               transform: translate(0, -50%) rotate(180deg);
            }
         }
      }
      &-content {
         font-weight: 500;
         font-size: 18px;
         line-height: 155%;
         color: $global;
         display: none;
         padding: 10px 0 25px 55px;
      }
   }
}
