.header {
	background: #fff;
	z-index: 500;
	@media (max-width: 375px) {
		margin-top: -35px;
	}
	@media (max-width: 320px) {
	}

	.container {
		max-width: 1790px;
		@media (max-width: 375px) {
			max-width: 375px;
			padding-left: 0 !important;
		}
		@media (max-width: 320px) {
			max-width: 320px;
		}
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 113px;
		background: #ffffff;
		@media (max-width: 375px) {
			height: 119px;
			margin-left: 5px;
		}
		@media (max-width: 320px) {
		}
	}
	&__btn {
		@media (max-width: 375px) {
			display: none;
		}
		@media (max-width: 320px) {
		}
	}
	&__logo {
		display: flex;
		align-items: center;
		img {
			@media (max-width: 375px) {
				height: 26px !important;
				width: 46px !important;
			}
			@media (max-width: 320px) {
			}
		}
		&-descr {
			width: 100%;
			max-width: 288px;
			font-weight: 400;
			font-size: 12px;
			line-height: 166%;
			color: #000;
			margin-left: 20px;
			@media (max-width: 375px) {
				width: 135px;
				height: 20px;
				font-size: 7px;
				line-height: 10px;
				margin-left: 3px !important;
			}

			@media (max-width: 320px) {
			}
			span {
				font-weight: 600;
				color: $purple;
			}
		}
	}
	&__info {
		width: 100%;
		max-width: 860px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 375px) {
			position: relative;
		}
		@media (max-width: 320px) {
		}
	}
	&__phone {
		@media (max-width: 375px) {
			position: absolute;
			top: 42px;
			left: -3px;
		}
		@media (max-width: 320px) {
			left: -20px;
		}
		&-descr {
			width: 167px;
			display: block;
			position: relative;
			font-weight: 600;
			font-size: 12px;
			line-height: 16px;
			color: #ffffff;
			background: #00c777;
			font-family: "Open Sans", sans-serif;
			border-radius: 200px;
			padding: 4px 0px 4px 26px;
			margin-bottom: 8px;
			@media (max-width: 375px) {
				width: 136px;
				font-size: 10px;
				line-height: 14px;
				padding: 4px 0px 4px 18px;
			}
			@media (max-width: 320px) {
			}
			&::before {
				content: "";
				width: 4px;
				height: 4px;
				background: #000;
				border-radius: 50%;
				position: absolute;
				top: 10px;
				left: 12px;
			}
		}
		&-link {
			position: relative;
			display: flex;
			align-items: flex-start;
			font-weight: 700;
			font-size: 18px;
			color: $purple;

			@media (max-width: 375px) {
				font-size: 12px;
				line-height: 14px;
			}
			@media (max-width: 320px) {
			}

			&::before {
				content: " ";
				background: black;
				width: 8px;
				height: 1px;
				transition: 0.3s ease 0s;
				transform: translate(0, -50%) rotate(-45deg);
				position: absolute;
				right: 38px;
				top: 10px;
			}
			&::after {
				content: " ";
				background: black;
				width: 8px;
				height: 1px;
				transition: 0.3s ease 0s;
				transform: translate(0, -50%) rotate(45deg);
				position: absolute;
				right: 43px;
				top: 10px;
			}
			&:hover {
				&::before {
					transform: translate(0, -50%) rotate(45deg);
				}
				&::after {
					transform: translate(0, -50%) rotate(-45deg);
				}
			}
			span {
				width: 185px;
				margin-left: 10px;
				position: relative;
			}
		}
		&-drpd {
			z-index: 500;
			position: absolute;
			width: 185px;
			height: 81px;
			top: -10px;
			left: -30px;
			border-radius: 2px;
			box-shadow: 0px 4px 18px 0px #00000014;
			display: none;
			font-weight: 700;
			font-size: 18px;
			color: $purple;

			@media (max-width: 375px) {
				height: 61px;
				font-size: 12px;
				line-height: 14px;
			}
			@media (max-width: 320px) {
			}
			p {
				margin: 40px 0 5px 25px;
				color: $purple;
				@media (max-width: 375px) {
					font-size: 12px;
					line-height: 14px;
				}
				@media (max-width: 320px) {
				}
			}
		}
	}
	&__accounts {
		display: flex;
		position: relative;
		align-items: center;
		font-weight: 500;
		font-size: 16px;
		line-height: 19px;
		color: $blue;
		@media (max-width: 375px) {
			font-size: 12px;
			line-height: 14px;
			position: absolute;
			top: -30px;
			left: -189px;
		}
		@media (max-width: 320px) {
		}
		svg {
			@media (max-width: 375px) {
				width: 14;
				height: 17px;
				margin-left: -8px;
			}
		}
		span {
			margin-right: 9px;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
		}
		&-menu {
			font-family: "Raleway";
			font-style: normal;
			font-weight: 500;
			font-size: 16px;
			line-height: 19px;
			@media (max-width: 375px) {
				font-size: 12px;
				line-height: 14;
			}
			@media (max-width: 320px) {
			}
			button {
				color: #4ac6e1;
				&::before {
					content: " ";
					background: #4ac6e1;
					width: 8px;
					height: 1px;
					transition: 0.3s ease 0s;
					transform: translate(0, -50%) rotate(-45deg);
					position: absolute;
					right: -20px;
					top: 10px;
					@media (max-width: 375px) {
						top: 82px;
					}
					@media (max-width: 320px) {
					}
				}
				&::after {
					content: " ";
					background: #4ac6e1;
					width: 8px;
					height: 1px;
					transition: 0.3s ease 0s;
					transform: translate(0, -50%) rotate(45deg);
					position: absolute;
					right: -15px;
					top: 10px;
					@media (max-width: 375px) {
						top: 82px;
					}
					@media (max-width: 320px) {
					}
				}
				&:hover {
					&::before {
						transform: translate(0, -50%) rotate(45deg);
					}
					&::after {
						transform: translate(0, -50%) rotate(-45deg);
					}
				}
			}
		}
		&-drpd {
			visibility: hidden;
			position: absolute;
			width: 219px;
			height: 98px;
			display: flex;
			flex-direction: column;
			background: #ffffff;
			box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.08);
			border-radius: 2px;
			justify-content: center;
			align-items: center;
			top: 25px;
			transition: all 0.3s ease;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
			div {
				width: 200px;
				height: 1px;
				border-top: 1px solid #e3e3e3;
				@media (max-width: 375px) {
				}
				@media (max-width: 320px) {
				}
			}
		}
		&-link {
			margin: 7px 0 5px 0;
			color: $global;
			transition: all 0.3s ease;
		}
	}
	&__languages {
		font-weight: 600;
		font-size: 14px;
		color: $purple;
		@media (max-width: 375px) {
			margin-left: 40px;
		}
		@media (max-width: 320px) {
			margin-left: 25px;
		}
		&-wrapper {
			max-width: 92px;
			min-height: 102px;
			border-radius: 0px 0px 26px 26px;
			padding: 0 8px;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
		}
		&-items {
			border-bottom: 1px solid $white;
			padding: 10px;
			display: flex;
			align-items: center;
			@media (max-width: 375px) {
			}
			@media (max-width: 320px) {
			}
			&:last-child {
				border-bottom: 0;
			}
			img {
				margin-right: 7px;
				@media (max-width: 375px) {
				}
				@media (max-width: 320px) {
				}
			}
		}
		&-point {
			display: flex;
			align-items: center;
			img {
				margin-right: 7px;
			}
		}
	}
	&__burger {
		cursor: pointer;
		width: 20px;
		height: 14px;
		span {
			display: block;
			background: #202223;
			height: 2px;
			margin-bottom: 5px;
			&:nth-child(1) {
				width: 16px;
			}
			&:nth-child(2) {
				width: 20px;
			}
			&:nth-child(3) {
				width: 14px;
				margin-bottom: 0;
			}
		}
	}
}

.header__menu {
	display: flex;
	flex-wrap: wrap;
	position: absolute;
	top: -141px;
	height: 75px;
	width: 100%;
	background: #503b5c;
	box-shadow: 0px 2px 16px rgba(223, 223, 223, 0.25);
	justify-content: center;
	text-align: center;
	transition: all 0.3s ease;
	@media (max-width: 375px) {
		height: 141px;
		padding: 24px 18px 0px 18px;
		text-align: justify;
		justify-content: space-around;
	}
	@media (max-width: 320px) {
	}
	a {
		margin: 19px 60px 0 0;
		font-family: "Raleway", sans-serif;
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 36px;
		color: #ffffff;
		position: relative;
		transition: all 0.4s linear;
		@media (max-width: 375px) {
			font-size: 12px;
			line-height: 12px;
			margin: 0 15px 5px 0;
		}
		@media (max-width: 320px) {
		}
	}
	:last-child {
		margin-left: 0 !important;
	}
}

.header__menu a::after {
	content: "";
	display: none;
	width: 100%;
	height: 1.5px;
	background-color: white;
	position: absolute;
	top: 35px;
	left: 0;
	transition: all 0.4s ease;
	@media (max-width: 375px) {
		top: 20px;
	}
	@media (max-width: 320px) {
	}
}

.header__menu-item a:hover::after {
	display: block;
}

.header__menu.active {
	top: 115px;
}

.header__languages li:hover ul {
	background: $purple;
}
.nav .button-dropdown {
	position: relative;
}

.nav li a {
	display: block;
	color: #333;
	background-color: #fff;
	padding: 10px 20px;
	text-decoration: none;
}

.nav li a span {
	display: inline-block;
	margin-left: 5px;
	font-size: 10px;
	color: #999;
}

.nav li a:hover,
.nav li a.dropdown-toggle.active {
	background-color: #289dcc;
	color: #fff;
}

.nav li a:hover span,
.nav li a.dropdown-toggle.active span {
	color: #fff;
}

.nav li .dropdown-menu {
	display: none;
	position: absolute;
	left: 0;
	padding: 0;
	margin: 0;
	margin-top: 3px;
	text-align: left;
}

.nav li .dropdown-menu.active {
	display: block;
}

.nav li .dropdown-menu a {
	width: 150px;
}

.header__phone-link:hover .header__phone-drpd {
	display: block;
}

.header__accounts-link:hover {
	color: #4ac6e1;
}

.header__accounts-menu:hover .header__accounts-drpd {
	visibility: visible;
}
